 body {
  background-color: var(--background-color);
  font-family: var(--font-family);
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  width: 100%;
  color: var(--font-color);
  /* scroll-snap-type: y mandatory;  */
  transition: var(--transition);
}

 /* * {
   border: 1px solid var(--color-primary);
 } */


 html {
   scroll-behavior: smooth;
   margin: 0;
   padding: 0;
   box-sizing: border-box;
   /* scroll-snap-type: y mandatory; */
   transition: var(--transition);
}

 h1, h2, h3, h4, h5, h6, p {
   margin: 0;
   padding: 0;
 }

 a {
    text-decoration: none;
 }

 .container {
   scroll-snap-type: y mandatory;
   transition: var(--transition);
   display: flex;
   flex-direction: column;
 }

.html__tag {
   font-size: 1.4rem;
   display: flex;
   align-items: center;
}

.noCursor {
   cursor: none;
}

.html__tag-after {
   margin-left: 2rem;
}

.html__tag-tag {
   text-align: center;
   color: var(--color-primary);
}

.html__tag-name {
   color: var(--accent-color);
   font-size: 1.5rem;
}

.App {
   scroll-snap-type: mandatory;
}

 section{
   min-height: 100vh;
   scroll-snap-align: center;
 }


.fadeOut__short {
   transform: translateY(5px);
   opacity: 0;
}

.fadeIn__short {
   transform: translateY(0);
   opacity: 1;
}

.section__padding {
   padding-inline: 2rem;
}

.fadeOut__rightEdge {
   transform: translate(-50%, -50%);
   opacity: 0;
}

.fadeIn__rightEdge {
   transform: translate(0, 0);
   opacity: 1;
}

#linkedin__icon,
#github__icon,
#youtube__icon {
   color: var(--color-primary);
}

@media screen and (max-width: 370px) {
   .container {
       font-size: 0.85rem;
   }
}