@import-normalize;
@import url('http://fonts.cdnfonts.com/css/lato');
@import url('http://fonts.cdnfonts.com/css/claris-sans');
@import url('http://fonts.cdnfonts.com/css/dashboard');


:root {
  --background-color: #ffffff;
  --accent-color: #616bf1;
  --accent-font: 'Claris Sans', sans-serif;
  --accent-font-size: 1.3rem;
  --accent-transparent: rgba(0, 0, 0, 0.1);
  --color-primary: #651fff;
  --border-radius: 24px;
  --box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
  --box-shadow-more: 0 0 20px rgba(0, 0, 0, 0.4);
  --button-color: #311B92;
  --color-github: #000000;
  --color-primary-title: #5018ca;
  --color-primary-icon: #8f74c9;
  --color-primary-darker: #3f2f61;
  --highlight-color: rgba(117, 117, 117, 0.54);
  --font-color: #000000;
  --font-family: 'Lato', sans-serif;
  --icon-color: #E1BEE7;
  --transition-fast: all 0.2s ease;
  --transition: all 0.3s ease;
  --transition-slow: all 0.6s ease;
}